@import "../vars";
@import "../mixins";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    @include variable(597);
    text-transform: uppercase;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.2em;
    }
    ._mob-view{
        display: none;
    }
}

.m-btn-primary{
    background-color: $primary;
    color: $black;
    border: 1px solid #000000;
    &:hover{
        background-color: darken($primary,20%);
        color: $black;
    }
}

.m-btn-outline{
    color: $black;
    border: 1px solid #000000;
    &:hover{
        background-color: $primary;
        color: $black;
    }
}

.swiper-button{
    color: #000;
    span{
        @include variable(148);
        font-size: 4.27em;
    }
}


@media screen and (max-width: 992px){
    .m-btn{
        span{
            font-size: .8em;
        }
        ._mob-view{
            display: unset;
        }
        ._desktop-view{
            display: none;
        }
    }


    .swiper-button{
        span{
            font-size: 2.53em;
        }
    }
}
