@import "../mixins";

.display1{
  @include variable(597);
  font-size: 3.2em;
  line-height: 140%;
  letter-spacing: 0.06em;
  text-transform: uppercase;
}

.display2{
  @include variable(597);
  font-size: 1.47em;
  line-height: 140%;
  letter-spacing: 0.06em;
  text-transform: uppercase;
}

.text15{
  font-size: 1em;
  line-height: 140%;
  letter-spacing: 0.06em;
  p{
    line-height: inherit;
    &:not(:last-child){
      margin-bottom: 1.5em;
    }
  }
}

.body-text{
  p{
    line-height: inherit;
    &:not(:last-child){
      margin-bottom: 10px;
    }
  }
}

@media screen and (max-width: 992px){
  .display1{
    font-size: 1.2em;
  }

  .display2{
    font-size: .67em;
  }

  .text15{
    font-size: .733em;
  }
}
