@import 'vars';
@import "mixins";
html {
    width: 100%;
    font-size: unquote("min(100vw, 100vw)");
}

.wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
    width: 100%;
    border: 1px solid $black;
    margin: 0 auto;
    @include variable(350);
}

.content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
}

body {
    font-family: $font1;
    font-weight: 400;
    color: $black;
    background: $secondary;
    font-size: .7813%;
    &.fixed-body {
        overflow-y: hidden;
    }
}

.container {
    margin: 0 auto;
    padding: 0 8em;
}

@media screen and (max-width: 992px){
    body{
        font-size: 4%;
    }

    .container{
        padding: 0 1.33em;
    }
}
