@import "../mixins";
.product{
  display: grid;
  grid-template-columns: 51% 49%;
  &__left{
    border-right: $border;
  }
  &__top{
    height: 6.4em;
    border-bottom: $border;
    display: grid;
    grid-template-columns: 33% 67%;
  }
  &__back{
    border: none;
    @include variable(148);
    span{
      font-size: 4.27em;
      letter-spacing: 0.06em;
    }
  }
  &__page-name{
    border-left: $border;
    @include center;
    background: $primary;
  }
  &__img{
    display: flex;
    align-items: center;
    height: 22.67em;
    padding: 3em 5.33em;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__props{
    display: grid;
    grid-template-columns: 55% 45%;
    border-top: $border;
  }
  .tab,
  &__prop,
  &__btn{
    height: 6.4em;
    &:not(:last-child){
      border-right: $border;
    }
  }
  .tab,
  &__prop{
    @include center;
    span{
      @include variable(597);
      font-size: 1.2em;
      letter-spacing: 0.06em;
      text-transform: uppercase;
    }
  }
  &__prop{
    justify-content: unset;
    padding: 0 5em;
  }
  &__right{
    display: flex;
    flex-direction: column;
  }
  &__action{
    display: grid;
    grid-template-columns: 50% 50%;
    border-top: $border;
    border-bottom: $border;
  }
  &__btn{
    border: none;
    font-size: 1em;
  }
}

.gallery{
  background-color: #fff;
  .swiper{
    &-slide{

    }
  }
  &__big{
    border-bottom: $border;
    position: relative;
    .swiper{
      &-slide{
        height: 66em;
        padding: 2em 7.33em;
        @include center;
        img{
          max-width: 100%;
          max-height: 100%;
          display: block;
        }
        a{
          @include center;
          width: 100%;
          height: 100%;
        }
      }
      &-button{
        &-prev{
          left: 4em;
        }
        &-next{
          right: 4em;
        }
      }
    }
  }
  &__small{
    &-img{
      position: relative;
      padding-bottom: 89%;
      img{
        position: absolute;
        top: 50%;
        left: 50%;
        max-width: 100%;
        max-height: 100%;
        object-fit: cover;
        transform: translate(-50%,-50%);
      }
    }
    .swiper{
      &-slide{
        border-left: $border;
        &:first-child{
          border-left: none;
        }
      }
    }
  }
}

.about-product{
  border-top: $border;
  padding: 3.33em 5.33em;
  height: 28em;
  &__inner{
    @include grid100Gap(3.2em);
    max-width: 43.4em;
    height: 100%;
    overflow: hidden;
  }
}

.product-detail{
  height: calc(100% - 22.67em - 12.8em - 28em);
  &__tabs{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    border-top: $border;
    border-bottom: $border;
  }
  &__content{
    height: calc(100% - 6.4em);
    padding: 2em 5.27em;
    overflow: hidden;
  }
}

.tab{
  padding: 0 .5em;
  text-align: center;
  cursor: pointer;
  &:hover,
  &._active{
    background-color: $primary;
  }
  &._active{
    pointer-events: none;
  }
  &-content{
    display: none;
    &._active{
      display: block;
    }
  }
}

.contain{
  &__text{
    max-width: 43.4em;
    p{
      line-height: inherit;
    }
  }
}

.use-way{
  @include grid100Gap(3em);
  padding: 1em 0;
  max-width: 50em;
  &__item{
    display: flex;
    align-items: center;
    justify-content: space-between;
    &:nth-child(even){
      flex-direction: row-reverse;
      .use-way__text{
        margin: 0 0 0 3em;
      }
    }
  }
  &__text{
    flex-grow: 1;
    margin-right: 3em;
  }
  &__decor{
    width: 6em;
    font-size: 1em;
    flex-shrink: 0;
    img{
      width: 100%;
    }
  }
}

.important{
  @include grid100Gap(3em);
  padding: 1em 0;
  max-width: 44em;
  &__group{
    @include grid100Gap(1.27em);
  }
}

.catalog--product{
  .catalog-card__img{
    height: 42.92em;
    border-bottom: none;
  }
}

.review2{
  &__left{
    padding: 3.13em 5.44em;
    text-align: center;
    border-right: $border;
  }
  &__title{
    font-style: italic;
    font-weight: 500;
    letter-spacing: 0.06em;
    text-transform: uppercase;
    font-size: 4.95em;
    line-height: 138%;
  }
  &__right{
    padding: 7.25em 5.33em 9.33em;
  }
  &__text{
    position: relative;
    max-width: 46.67em;
    padding-left: 3em;
    @include grid100Gap(1.33em);
    &:before{
      content: '“';
      position: absolute;
      left: 0;
      top: 0;
      font-size: 94px;
      letter-spacing: 0.06em;
      @include variable(148);
      transform: translateY(-30%);
    }
  }
  &__by{
    text-align: right;
    .display2{
      font-size: 1em;
    }
  }
}


@media screen and (max-width: 992px){
  .product{
    grid-template-columns: 100%;
    &__top{
      height: 4em;
      grid-template-columns: 31% 69%;
    }
    &__back{
      span{
        font-size: 2.53em;
      }
    }
    &__page-name{
      span{
        font-size: 1em;
      }
    }
    &__prop,
    .tab,
    &__btn{
      height: 4em;
    }
    .tab{
      span{
        font-size: .67em;
      }
    }
    &__img{
      height: 12.33em;
      padding: 2.47em 1.33em;
    }
    &__prop{
      padding: 0 1.33em;
      span{
        font-size: .8em;
      }
    }
  }

  .gallery{
    &__big{
      .swiper{
        &-slide{
          height: 19.47em;
          width: 19.47em;
          padding: 1.67em;
          border-right: $border;
        }
        &-button{
          &-prev{
            left: 1.67em;
          }
          &-next{
            right: 1.67em;
          }
          &-disabled{
            opacity: 0 !important;
          }
        }
      }
    }
    &__small{
      display: none;
    }
  }

  .about-product{
    height: unset;
    padding: 2.66em 1.33em 3.33em;
    &__inner{
      gap: 1.28em;
    }
    &__title{
      font-size: 1em;
    }
  }

  .product-detail{
    height: unset;
    &__content{
      padding: 1.66em 1.33em;
      overflow: unset;
      height: unset;
    }
  }

  .use-way{
    gap: 2em;
    padding: 0;
    &__decor{
      width: 4em;
    }
  }

  .important{
    gap: 2em;
    padding: 0;
    &__group{
      gap: .5em;
    }
  }

  .catalog--product{
    .catalog-card__img{
      height: 27.34em;
    }
  }

  .review2{
    &__left{
      padding: 1.66em 1.33em 0;
      text-align: left;
      border-right: none;
    }
    &__title{
      font-size: 2.8em;
    }
    &__right{
      padding: 5.33em 1.33em 1.66em;
    }
    &__text{
      padding-left: 2.27em;
      gap: 1em;
      &:before{
        font-size: 4.66em;
      }
    }
    &__by{
      .display2{
        font-size: .73em;
      }
    }
  }
}
