@import "../mixins";

.fg {
  position: relative;
  margin: 0;
  input {
    border: none;
    border-bottom: 1px solid #000000;
    padding: 0 .6em;
    height: 3.67em;
    width: 100%;
    font-size: 1em;
    background-color: transparent;
    color: #000;
    text-transform: uppercase;
    @include variable(500);
    &::placeholder {
      color: #A6A6A6;
    }
  }
}

.check {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        // background-color: $green;
        border: 3px solid #fff;
      }
    }
    &:disabled+label {
      color: #AAAAAA;
      pointer-events: none;
    }
  }
  label {
    padding-left: 40px;
    position: relative;
    min-height: 20px;
    font-size: 14px;
    line-height: 20px;
    cursor: pointer;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      border-radius: 2px;
      background: rgba(255, 255, 255, 0.01);
      border: 1px solid rgba(255, 255, 255, 0.45);
      width: 20px;
      height: 20px;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      transition: all .100s;
    }
  }
}

@media screen and (max-width: 992px){
  .fg{
    input{
      font-size: .47em;
    }
  }
}
