@import "../mixins";
.main{
  margin-top: -5.33em;
  display: grid;
  grid-template-columns: auto 76.7em;
  &__img{
    position: relative;
    &:before{
      content: '';
      position: absolute;
      width: 100%;
      height: 1px;
      background-color: #fff;
      top: 5.33em;
      left: 0;
    }
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__content{
    padding: 11.5em 0 0;
  }
  &__logo{
    width: 26.7em;
    margin: 0 auto 4.33em;
    svg{
      width: 100%;
      height: auto;
    }
  }
  &__title{
    padding: 0 1em;
    text-align: center;
  }
  &__nav{
    display: grid;
    grid-template-columns: 40% auto;
    margin-top: 4.33em;
  }
  &__btn{
    height: 6.4em;
    &:nth-child(2){
      width: calc(100% + 1px);
      margin-left: -1px;
    }
  }
  &__text{
    padding: 4.33em;
    text-align: center;
  }
}

.about{
  display: grid;
  grid-template-columns: 33.125% 25.31% auto;
  border-top: 1px solid #000000;
  &__img{
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__moto{
    padding: 4em 3.33em;
    display: flex;
    align-items: center;
    &-text{
      font-size: 8.67em;
      @include variable(500);
      font-style: italic;
    }
  }
  &__content{
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding: 5.33em 8em 5.33em 1.47em;
  }
  &__info{
    @include grid100;
    gap: 3.2em;
  }
}

.decor{
  display: flex;
  justify-content: space-between;
  padding: 3.13em 4.67em;
  border-top: 1px solid #000;
  &__item{
    font-size: 4.27em;
    line-height: 141%;
    letter-spacing: 0.05em;
    color: #000000;
    font-weight: 200;
    font-style: italic;
  }
}

.advantages{
  display: grid;
  grid-template-columns: auto 40%;
  border-top: 1px solid #000000;
  &__img{
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__content{
    padding: 9.33em 4.33em 7.6em 5.4em;
  }
  &__title{
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 0.06em;
    max-width: 42.9em;
    margin: 0 auto;
    font-size: 1em;
  }
  &__items{
    margin-top: 7.33em;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 1.53em;
  }
}

.catalog{
  border-top: 1px solid #000000;
  overflow: hidden;
  &__top{
    text-align: center;
    padding: 3.67em 0;
    @include grid100;
    gap: 2.2em;
  }
  &__slider{
    border-top: 1px solid #000000;
  }
  @media screen and (min-width: 993px){
    .swiper{
      &-wrapper{
        transform: unset !important;
        display: grid;
        grid-template-columns: calc(100% / 3) calc((100% - (100% / 3)) / 3) calc((100% - (100% / 3)) / 3) calc((100% - (100% / 3)) / 3);
      }
      &-slide{
        width: 100% !important;
      }
      &-button{
        display: none;
      }
    }
  }
}

.catalog-card{
  &__top{
    height: 6.5625em;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__img{
    border-top: 1px solid #000;
    border-bottom: 1px solid #000;
    height: 31.25em;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__desc{
    padding: 2.73em 4.66em;
    height: 11.67em;
    &-text{
      overflow: hidden;
      display: -webkit-box;
      -webkit-line-clamp:5;
      -webkit-box-orient: vertical;
    }
  }
}

.review{
  border-top: 1px solid #000000;
  overflow: hidden;
  position: relative;
  &__nav{
    border-top: 1px solid $black;
    position: absolute;
    width: 60%;
    right: 0;
    bottom: 0;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    background: $secondary;
  }
  .swiper{
    &-button{
      width: 100%;
      height: 6.4em;
      position: static;
      margin: 0;
      &:not(:last-child){
        border-right: 1px solid $black;
      }
    }
  }
}

.values{
  border-top: 1px solid #000;
  display: grid;
  grid-template-columns: 46.8% auto auto;
  &__title{
    font-size: 6em;
    line-height: 138%;
    letter-spacing: 0.06em;
    font-weight: 500;
    font-style: italic;
  }
  @media screen and (min-width: 993px){
    &__info{
      padding: 5.67em 4.27em 5.67em 8em;
    }
    .advantage-card{
      border: 1px solid #000;
      border-right: none;
      border-bottom: none;
      padding: 5.33em 3.2em 4.47em 5.3em;
      &:nth-child(2){
        grid-column: 2/4;
        padding: 7.87em 4.4em 6.73em 5.3em;
        border-top: none;
        border-bottom: none;
      }
      &:nth-child(3){
        padding-left: 8em;
      }
    }
  }
}

.play{
  display: grid;
  grid-template-columns: 40% auto;
  border-top: 1px solid #000;
  &__form{
    padding: 13.73em 3em 11.7em;
    text-align: center;
    background: #FFFFFF;
    border-right: 1px solid #000;
    &-info{
      @include grid100;
      gap: 3.7em;
    }
    &-title{
      font-size: 3.33em;
      line-height: 140%;
      font-style: italic;
      font-weight: 500;
      letter-spacing: 0.05em;
    }
    &-desc{
      font-size: 1em;
    }
  }
  &__field{
    margin-top: 6em;
    input{
      padding-right: 10em;
    }
  }
  &__btn{
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    span{
      font-size: 1em;
    }
  }
  &__content{
    position: relative;
  }
  &__img{
    height: calc(100% - 11.5em);
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__title{
    position: absolute;
    width: 100%;
    padding: 2em;
    color: rgba(255, 255, 255, 0.6);
    z-index: 2;
    top: 0;
    left: 0;
    font-size: 1em;
    text-align: center;
    span{
      display: block;
      letter-spacing: 0.06em;
      font-style: italic;
      font-weight: 500;
      font-size: 7.33em;
      line-height: 118%;
    }
  }
  &__text{
    height: 11.5em;
    display: flex;
    align-items: center;
    padding: 4em 5em;
    text-align: center;
    border-top: 1px solid #000;
  }
  &__desc{
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:3;
    -webkit-box-orient: vertical;
  }
}

.program{
  border-top: 1px solid #000;
  display: grid;
  grid-template-columns: 40% auto;
  &__img{
    border-right: 1px solid #000;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__content{
    padding: 6.93em 3.47em 11.33em 13.7em;
    position: relative;
  }
  &__list{
    margin-top: 3.6em;
    ol{
      counter-reset: listNum;
      &>li{
        font-size: 1.47em;
        line-height: 364%;
        display: flex;
        @include variable(234);
        &:before{
          counter-increment: listNum;
          content: '(' counter(listNum) ')';
          margin-right: 2em;
          flex-shrink: 0;
        }
      }
    }
  }
  &__link{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 6.33em;
    border-left: none;
    border-right: none;
    border-bottom: none;
  }
  &__img2,
  &__img3{
    position: absolute;
    top: 2.33em;
    right: 2.89em;
    width: 24em;
  }
  &__img3{
    display: none;
  }
}


@media screen and (max-width: 992px){
  .main{
    grid-template-columns: 100%;
    color: #fff;
    position: relative;
    &__content{
      padding: 12.8em 1.33em 4.73em;
      position: relative;
      z-index: 2;
    }
    &__img{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      overflow: hidden;
      font-size: 1em;
      img{
        width: auto;
        object-fit: unset;
      }
    }
    &__logo{
      width: 18.73em;
      margin-bottom: 2em;
      path{
        fill: #fff;
      }
    }
    &__text{
      padding: 0;
      margin-top: 2em;
      br{
        display: none;
      }
    }
    &__nav{
      margin: 0;
      position: absolute;
      top: 100%;
      left: 0;
      width: 100%;
      grid-template-columns: 1fr 1fr;
    }
    &__btn{
      height: 4em;
    }
  }

  .about{
    margin-top: calc(4em - 1px);
    grid-template-columns: 100%;
    position: relative;
    &__moto{
      position: absolute;
      top: 0;
      right: 0;
      width: 11.5em;
      padding: 2.53em 1.33em;
      letter-spacing: 0.06em;
      color: rgba(255, 255, 255, 0.6);
      &-text{
        font-size: 3.2em;
      }
    }
    &__content{
      padding: 0 1.33em 2.4em;
      margin-top: -3.27em;
      position: relative;
      z-index: 2;
    }
    &__title{
      color: #fff;
      @include variable(500);
    }
    &__info{
      gap: 4.73em;
    }
  }

  .decor{
    padding: 1.2em .6em;
    &__item{
      font-size: 1.07em;
    }
    ._mob-hide{
      display: none;
    }
  }

  .advantages{
    grid-template-columns: 100%;
    &__img{
      order: -1;
      position: relative;
      &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(0deg, rgba(0, 0, 0, 0.4) 46.86%, rgba(0, 0, 0, 0) 100%);
      }
    }
    &__content{
      padding: 0 1.33em 4em;
    }
    &__title{
      margin-top: -8.73em;
      position: relative;
      z-index: 2;
      color: #fff;
      text-transform: unset;
    }
    &__items{
      grid-template-columns: 100%;
      gap: 2.67em;
      margin-top: 8.73em;
    }
  }

  .catalog{
    &__top{
      gap: 1.47em;
      padding: 2.67em 0;
    }
    &__slider{
      position: relative;
    }
    .swiper{
      &-slide{
        width: 19.6em;
      }
      &-button{
        color: #000000;
        font-size: 1em;
        top: 13.7em;
        margin-top: 0;
        &-disabled{
          opacity: 0 !important;
        }
      }
    }
  }

  .catalog-card{
    &__top{
      height: 4em;
      .display2{
        font-size: 1em;
      }
    }
    &__img{
      height: 19.47em;
    }
    &__desc{
      padding: .87em 1.33em;
      height: 7.87em;
      &-text{
        -webkit-line-clamp: 7;
        font-size: .6em;
      }
    }
  }

  .review{
    &__nav{
      width: 100%;
      position: static;
    }
    .swiper{
      &-button{
        height: 4em;
      }
    }
  }

  .values{
    grid-template-columns: 100%;
    gap: 2.67em;
    padding: 2.47em 1.33em 4em;
    &__info{
      margin-bottom: 2em;
    }
    &__title{
      font-size: 2.8em;
      line-height: 138%;
    }
  }

  .play,
  .program{
    grid-template-columns: 100%;
    &__img{
      padding-bottom: 100%;
      img{
        position: absolute;
        left: 0;
        top: 0;
      }
      &:before{
        pointer-events: none;
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 2;
        background: linear-gradient(0deg, rgba(0, 0, 0, 0.2) 37.6%, rgba(0, 0, 0, 0) 100%);
      }
    }
  }

  .play{
    &__content{
      order: -1;
    }
    &__info{
      position: absolute;
      top: 0;
      left: 0;
      padding: 1.33em;
      height: 100%;
      width: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      text-align: center;
      z-index: 3;
    }
    &__title{
      position: static;
      padding: 0;
      margin-bottom: 2em;
      span{
        line-height: 100%;
        font-size: 3.2em;
      }
    }
    &__text{
      color: #fff;
      height: unset;
      border: none;
      padding: 0;
    }
    &__desc{
      -webkit-line-clamp: 6;
    }
    &__form{
      padding: 2.67em 2.27em;
      &-info{
        gap: 2.13em;
      }
      &-title{
        font-size: 1.8em;
      }
      &-desc{
        font-size: .53em;
      }
    }
    &__field{
      margin-top: 1em;
    }
    &__btn{
      font-size: 1em;
      span{
        font-size: .47em;
      }
    }
  }

  .program{
    padding-bottom: 4em;
    position: relative;
    overflow: hidden;
    &__content{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: calc(100% - 4em);
      z-index: 3;
      padding: 2.66em 1.33em;
    }
    &__link{
      bottom: unset;
      top: 100%;
      height: 4em;
      white-space: unset;
      padding: 0 6em;
      text-align: center;
      background-color: #B2C3B7;
      span{
        line-height: 142%;
      }
    }
    &__title{
      color: rgba(255, 255, 255, 0.6);
      font-size: 2.2em;
      line-height: 100%;
      font-style: italic;
      font-weight: 500;
    }
    &__img2{
      display: none;
    }
    &__img3{
      width: 9.6em;
      top: unset;
      right: 1.33em;
      bottom: 1.67em;
    }
    &__list{
      color: #FFFFFF;
      margin-top: 2em;
      ol{
        li{
          font-size: .73em;
          line-height: 136%;
          &:not(:last-child){
            margin-bottom: 2em;
          }
          &:before{
            min-width: 3em;
            margin-right: .5em;
          }
        }
      }
    }
  }
}
