@import "../mixins";

.header{
  @include variable(400);
  border-bottom: 1px solid $black;
  position: relative;
  z-index: 10;
  &__inner{
    display: flex;
    align-items: center;
  }
  &__logo{
    display: block;
    width: 7.75em;
    margin-right: 18em;
    flex-shrink: 0;
    z-index: 16;
    position: relative;
    svg{
      width: 100%;
      height: auto;
      display: block;
    }
  }
  &__mob{
    display: flex;
    align-items: center;
    margin-left: auto;
  }
  &__shops{
    display: flex;
    align-items: center;
  }
  &__shop{
    margin-right: 2em;
  }
  &__social{
    width: 1.47em;
    display: block;
    img{
      width: 100%;
    }
  }
  &--index{
    .nav{
      color: #fff;
    }
  }
}

.nav{
  display: flex;
  align-items: center;
  justify-content: space-between;
  //flex-grow: 1;
  margin-left: 5.33em;
  padding-left: 5.33em;
  height: 5.33em;
  border-left: 1px solid $black;
  width: 68.7em;
  &__link{
    text-transform: uppercase;
    &:hover{
      text-decoration: underline;
    }
  }
}

.burger{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    padding: 0;
    &__inner{
      justify-content: space-between;
      position: relative;
      height: 5.33em;
      font-size: 1em;
    }
    &__logo{
      width: 10em;
      margin: 0;
    }
    &__mob{
      position: fixed;
      top: 0;
      left: 0;
      z-index: 15;
      width: 100%;
      height: 100vh;
      display: block;
      padding: 5.33em 0 2em;
      background: #fff;
      border: 1px solid #393939;
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
    }
    &__shops{
      border-top: 1px solid rgba($black,.6);
      padding: 1.47em;
      flex-direction: column;
    }
    &__shop{
      margin-right: auto;
      font-size: .8em;
      &:not(:last-child){
        margin-bottom: .67em;
      }
    }
    &__social{
      display: none;
    }
    &--index{
      .header{
        &__logo{
          path{
            fill: #fff;
          }
        }
        &__mob{
          background: linear-gradient(0deg, rgba(0, 0, 0, 0.6) 35.88%, rgba(0, 0, 0, 0) 100%);
          color: #fff;
          backdrop-filter: blur(4.5px);
        }
        &__shops{
          border-color: rgba(255, 255, 255, 0.6);
        }
      }
      .nav{
        border-color: rgba(255, 255, 255, 0.6);
      }
      .burger{
        rect{
          fill: #fff;
        }
      }
    }
    &._open-menu{
      .header__logo{
        position: relative;
      }
      .header__mob{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }

  .burger{
    width: 1.07em;
    position: relative;
    //right: 0;
    //top: 50%;
    //transform: translateY(-50%);
    display: flex;
    z-index: 16;
    &:before{
      content: '';
      width: 200%;
      height: 200%;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
    }
    svg{
      width: 100%;
      height: auto;
    }
  }

  .nav{
    border-top: 1px solid rgba($black,.6);
    border-bottom: 1px solid rgba($black,.6);
    border-left: none;
    padding: 1.47em;
    flex-direction: column;
    justify-content: unset;
    margin: 0;
    height: unset;
    width: 100%;
    &__link{
      margin-right: auto;
      font-size: .8em;
      &:not(:last-child){
        margin-bottom: .67em;
      }
    }
  }
}
