@import "../mixins";

.advantage-card{
  &__decor{
    font-style: italic;
    font-weight: 200;
    font-size: 3.07em;
    line-height: 141%;
    letter-spacing: 0.05em;
  }
  &__info{
    @include grid100;
    gap: 1.33em;
    margin-top: 3.53em;
  }
  &__title{
    line-height: 100%;
    min-height: 3em;
  }
}

.catalog-card2{
  border-left: 1px solid #000000;
  height: 100%;
  position: relative;
  &:last-child{
    &:after{
      content: '';
      position: absolute;
      top: 0;
      left: 100%;
      width: 1px;
      height: 100%;
      background: #000;
    }
  }
  &__top{
    height: 6.5625em;
    display: flex;
    align-items: center;
    justify-content: center;
    &._accent{
      background-color: $primary;
    }
  }
  &__img{
    border-top: 1px solid #000;
    border-bottom: 1px solid #000;
    height: 31.25em;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 2.13em;
    background-color: #fff;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__desc{
    height: 5.6em;
    padding: 1em 1.8em;
    display: flex;
    align-items: center;
    background-color: #fff;
    &-text{
      font-size: .93em;
      line-height: 129%;
      letter-spacing: 0.06em;
      text-transform: uppercase;
      overflow: hidden;
      display: -webkit-box;
      -webkit-line-clamp:3;
      -webkit-box-orient: vertical;
    }
  }
  &__link{
    height: 6.07em;
    border-left: none;
    border-right: none;
    border-top: 1px solid #000;
    border-bottom: none;
  }
}

.review-card{
  display: grid;
  grid-template-columns: 40% auto;
  &__img{
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__content{
    border-left: 1px solid #000000;
    padding: 10.25em 8.53em 14.93em;
  }
  &__info{
    @include grid100;
    gap: 5.33em;
    text-align: center;
  }
  &__text{
    font-synthesis: none;
    @include variableItal(234);
    font-size: 2.4em;
    line-height: 139%;
    letter-spacing: 0.06em;
    text-transform: uppercase;
  }
  &__by{
    margin: 0 auto;
    max-width: 20.4em;
    @include grid100;
    gap: .73em;
  }
  &__name,
  &__who{
    font-size: 1em;
    line-height: 120%;
    letter-spacing: 0.06em;
  }
  &__name{
    @include variable(597);
  }
  &__who{
    @include variable(234);
  }
}


@media screen and (max-width: 992px){
  .advantage-card{
    &__decor{
      font-size: 1.33em;
      line-height: 145%;
    }
    &__info{
      margin-top: 1.47em;
      gap: 1.53em;
    }
    &__title{
      min-height: unset;
      font-size: 1em;
    }
  }

  .catalog-card2{
    &__top{
      height: 4em;
      .display2{
        font-size: 1em;
      }
    }
    &__img{
      height: 19.47em;
    }
    &__desc{
      padding: .5em 1.6em;
      height: 4em;
      &-text{
        font-size: .69em;
      }
    }
    &__link{
      height: 3.87em;
    }
  }

  .review-card{
    grid-template-columns: 100%;
    position: relative;
    &__img{
      position: relative;
      padding-bottom: 100%;
      img{
        position: absolute;
        top: 0;
        left: 0;
      }
      &:before{
        pointer-events: none;
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 2;
        background: linear-gradient(0deg, rgba(0, 0, 0, 0.4) 46.86%, rgba(0, 0, 0, 0) 100%);
      }
    }
    &__content{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 3em;
      color: #fff;
      z-index: 3;
      display: flex;
      align-items: center;
    }
    &__info{
      gap: 2.2em;
    }
    &__text{
      font-size: 1em;
    }
    &__by{
      gap: .32em;
      max-width: 8.67em;
    }
    &__name,
    &__who{
      font-size: .42em;
    }
  }
}
