@import "../mixins";
.catalog-page{
  &__nav{
    display: grid;
    grid-template-columns: repeat(4,calc(100% / 3));
  }
  &__link{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 6.4em;
    @include variable(500);
    span{
      font-size: 1.47em;
    }
    img{
      height: 1.6em;
      display: none;
    }
    &:not(:last-child){
      border-right: 1px solid #000;
    }
    &._active{
      pointer-events: none;
      img{
        display: unset;
      }
      span{
        display: none;
      }
    }
    &._type1{
      &._active,
      &:hover{
        background-color: $primary;
      }
    }
    &._type2{
      &._active,
      &:hover{
        background-color: #D7E3E7;
      }
    }
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(4,25%);
  }
  .catalog-card2{
    border-bottom: none;
    @media screen and (min-width: 993px){
      &:nth-child(4)~.catalog-card2{
        margin-top: -1px;
        .catalog-card2__link{
          border-bottom: none;
        }
      }
    }
  }
  &__wrap{
    display: grid;
    grid-template-columns: auto 40%;
    border-top: 1px solid #000000;
  }
  &__img{
    border-left: 1px solid #000000;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__content{
    padding: 6.2em 8em;
    display: flex;
    flex-direction: column;
  }
  &__title{
    font-style: italic;
    font-weight: 500;
    font-size: 1em;
    span{
      font-size: 6.2em;
      line-height: 118%;
    }
    svg{
      display: block;
      height: 4.4em;
      width: auto;
      margin-top: 1.3em;
    }
  }
  &__text{
    margin-top: auto;
    padding-top: 2em;
    max-width: 43.4em;
    @include variable(500);
  }
  &._type2{
    .catalog-card2{
      &__link{
        background-color: #D7E3E7;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .catalog-page{
    overflow: hidden;
    &__link{
      height: 4em;
      span{
        font-size: .8em;
      }
      img{
        height: .6em;
      }
    }
    &__items{
      grid-template-columns: repeat(2,50%);
    }
    .catalog-card2{
      border-bottom: none;
      &:nth-child(2)~.catalog-card2{
        margin-top: -1px;
        .catalog-card2__link{
          border-bottom: none;
        }
      }
      &__img{
        padding: .8em 1.6em;
        height: 13.53em;
      }
      &__desc{
        padding-left: .8em;
        padding-right: .8em;
        &-text{
          font-size: .53em;
        }
      }
    }
    &__wrap{
      grid-template-columns: 100%;
    }
    &__img{
      order: -1;
      position: relative;
      border-left: none;
      border-bottom: 1px solid #000;
      &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(0deg, rgba(0, 0, 0, 0.4) 46.86%, rgba(0, 0, 0, 0) 100%);
      }
    }
    &__content{
      padding: 0 0 2.6em;
      margin-top: -8.93em;
      position: relative;
      z-index: 3;
    }
    &__title{
      padding: 0 3.33em;
      color: rgba(255, 255, 255, 0.6);
      path{
        fill:rgba(255, 255, 255, 0.6);
      }
      span{
        font-size: 3.2em;
      }
      svg{
        margin-top: .66em;
        height: 2.26em;
      }
    }
    &__text{
      padding: 4.4em 1.33em 0;
    }
  }
}
