@import "../mixins";
.footer{
  display: grid;
  grid-template-columns: 40% auto auto;
  border-top: 1px solid #000;
  &__logo{
    display: block;
    width: 24.73em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__links,
  &__contacts{
    &-items{
      @include grid100;
      gap: 1em;
    }
    &-item{
      @include variable(500);
      font-size: 1em;
      line-height: 120%;
      letter-spacing: 0.06em;
    }
  }
  &__contacts{
    padding: 7.26em 8em;
    border-right: 1px solid #000;
  }
  &__links{
    padding: 7.26em 8em 7.26em 0em;
    text-align: right;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  &__img{
    padding: 0 3.33em;
    display: flex;
    align-items: center;
  }
}


@media screen and (max-width: 992px){
  .footer{
    grid-template-columns: 58% 42%;
    grid-template-rows: 36% 64%;
    &__links,
    &__contacts{
      &-item{
        font-size: .47em;
        line-height: 143%;
      }
    }
    &__contacts{
      grid-column: 2/3;
      grid-row: 1/3;
      border-right: none;
      border-left: 1px solid #000;
      padding: 2.33em 1.33em;
      text-align: right;
      &-items{
        gap: 1.5em;
      }
    }
    &__links{
      border-top: 1px solid #000;
      padding: 1.33em;
      &-items{
        gap: .5em;
      }
    }
    &__img{
      padding: 1.33em;
    }
    &__logo{
      width: 6em;
    }
  }
}
